










import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import JobEditor from "@/components/misc/JobEditor.vue";
import {
  Overwrite,
  IServerResponse,
  ICreateBackupJobResponseSuccess
} from "@/shared";
import {
  repositoriesModule,
  jobsModule,
  routerModule,
  adminModule
} from "@/store/modules";
import { IJob } from "@/store/modules/JobsModule";
import { IBackupJobWithClientId } from "./Client.vue";
import { IRepository } from "../store/modules/RepositoriesModule";

@Component({
  components: {
    JobEditor
  }
})
export default class NewJobView extends Mixins() {
  path = "";
  clientrepos: IRepository[] | false = [];

  mounted() {
    if (this.clientId) {
      this.fetchClientRepos();
    }
  }
  get clientId(): string {
    return routerModule.params.clientId;
  }

  async fetchClientRepos() {
    const repos = await adminModule.getRepos(this.clientId);
    if (repos) {
      this.clientrepos = repos;
      console.log("resultfromfetching", repos);
      return;
    }
    this.clientrepos = false;
  }

  async onSubmit(
    job: Overwrite<
      IJob,
      {
        id?: number;
      }
    >
  ) {
    // TODO: infer args from other class
    // TODO: trycatch
    if (!this.clientId) {
      const resp = await jobsModule.createJob(job);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/jobs/${resp.payload.job.id}`
        });
      }
    } else {
      console.log("client!!!");
      const cjob: IBackupJobWithClientId = {
        clientId: this.clientId,
        clientjob: job
      };
      const resp = await adminModule.createBackupJobOnClient(cjob);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/admin/client/${this.clientId}`
        });
      }
    }
  }
}
